    line-height: 1.43;
    color: #666670;
}
.page-content.sign-page .box .captcha .recaptcha {
    float: right;
}
.page-content.sign-page .box .agreement {
    padding-top: 23px;
    padding-bottom: 25px;
}
@media (max-width: 559px) {
    .page-content.sign-page .box {
        width: 100%;
        padding-top: 25px;
        padding-left: 10px;
        padding-right: 10px;
    }
}
.page-content.sign-page .clear {
    clear: both;
}



